@import url(https://fonts.googleapis.com/css?family=Tomorrow&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  height: 2000px;
  color: #000;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container {
  width: 900px;
  margin: auto;
}

.header-layout {
  padding-bottom: 80px;
  background-color: #ffffff;
}
.header {
  display: flex;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  z-index: 1;
}
content {
  height: 2000px;
}
.logo img {
  height: 100%;
  margin-left: 20px;
}
.menu-item {
  display: flex !important;
  justify-content: space-around !important;
}
.search-bar > span {
  padding-top: 15px;
  width: 350px;
}
.search-bar .ant-input-group-addon button {
  padding: 0px;
  padding-left: 10px;
}
.search-wrapper .ant-menu.ant-menu-dark .ant-menu-item-selected,
.search-wrapper .ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
  background-color: transparent;
}
.radio-group-wrapper {
  margin-left: 15px;
}
.radio-group .radio-button span {
  color: #ffffff;
}
.ant-radio-input__placeholder {
  color: #000;
}
.header .user-img img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
#logout {
  background-color: transparent;
  border: none;
}
#logout i {
  font-size: 24px;
  color: #ffffff;
}
.search-bar {
  display: flex;
  max-height: 60px;
}
.search-input {
  color: #000;
  width: 250px;
  height: 35px;
  border: 0px;
  border-radius: 3px;
  padding: 10px;
  margin-right: 10px;
  margin-top: 15px;
}
.search-btn {
  height: 35px;
  margin-top: 15px;
  border: 0;
  border-radius: 4px;
  line-height: 31px;
}
.ant-search-btn {
  height: 34px;
}

.tabs-wrapper .tabs .ant-tabs-bar {
  margin-left: auto;
  margin-right: auto;
  width: 250px !important;
}
.tabs-wrapper .tabs .dragger {
  height: 0 !important;
}
.tabs-wrapper .tabs textarea,
.tabs-wrapper .tabs .dragger {
  min-height: 150px !important;
  width: 100%;
}

.tabs-wrapper .tabs .post-btn,
.tabs-wrapper .tabs .img-btn {
  display: block;
  margin: auto;
  margin-top: 15px;
}

.post-wrapper {
  text-align: left;
  margin-top: 50px;
}
.post-wrapper .post {
  display: flex;
  padding: 15px;
  margin-top: 30px;
  border: 1px solid transparent;
  border-radius: 8px;
  box-shadow: 2px 6px 17px 0px rgba(9, 9, 10, 0.21);
}
.post-wrapper .user-img img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
.post-wrapper .post-body {
  color: #000;
  font-size: 16px;
  margin-top: 5px;
}
.post-wrapper .post-writer {
  font-weight: bold;
}
.post-wrapper .post-data {
  margin-left: 30px;
  padding-top: 10px;
}
.post-wrapper .user-time span {
  display: block;
}

.hide {
  display: none;
}
.post-writer {
  cursor: pointer;
}
.post-img {
  width: 90%;
  margin: 5px;
}

.user-profile {
  text-align: left !important;
}
#user-profile-heading {
  font-family: monospace;
  text-align: center;
}
.general-wrapper,
.bio-wrapper,
.portfolio-wrapper {
  margin-top: 25px;
}
.general-border {
  display: flex;
  padding: 20px 20px 20px 20px;
  border: 1px solid transparent;
  border-radius: 8px;
  box-shadow: 2px 6px 17px 0px rgba(9, 9, 10, 0.21);
  margin-top: 10px;
}
.bio-info,
.portfolio-info {
  padding: 20px;
  border: 1px solid transparent;
  border-radius: 8px;
  box-shadow: 2px 6px 17px 0px rgba(9, 9, 10, 0.21);
  margin-top: 10px;
}
.general .user-img img {
  width: 150px;
  height: 150px;
}
.general .user-name {
  display: block;
  margin-top: 10px;
}
.general .social-links {
  list-style: none;
  padding: 0px;
  justify-content: space-around;
  padding: 0px 5px;
  margin-top: 10px;
  margin-bottom: 0px;
}
.general .social-links .social-item {
  font-size: 20px;
}
.general-info {
  margin-left: 20px;
  padding-top: 20px;
  font-family: sans-serif;
  color: #000;
  font-size: 16px;
}
.bio-content {
  font-family: sans-serif;
  color: #000;
  font-size: 16px;
}
.skills-id {
  display: block;
  font-weight: bold;
  font-size: 16px;
  color: #1890ff;
}
.skills-tags {
  margin-top: 10px;
}
.project-item {
  border-bottom: 1px solid #d9d9d9 !important;
  margin-bottom: 12px;
}
.project-item .ant-collapse-content-box {
  background-color: #ffffff;
}

.section-id {
  color: #1890ff;
  font-family: monospace;
  font-weight: bold;
  font-size: 22px;
}
.project-decription {
  font-family: sans-serif;
  color: #000;
  font-size: 14px;
}
.editt {
  display: flex;
  justify-content: space-between;
}
.edit-profile-text {
  color: #1890ff !important;
  font-family: monospace;
  font-weight: bold;
  font-size: 16px;
}
.clear-btn {
  color: #1890ff;
  border-color: #1890ff;
  padding: 0px 10px;
  margin-left: 10px;
}
.social-links {
  list-style: none;
  padding: 0px;
  display: flex;
  justify-content: space-around;
  padding: 0px 15px;
  margin-top: 10px;
}

.ant-form-item-label {
  text-align: left !important;
}
.ant-tabs-nav-scroll {
  text-align: center;
}
#edit-profile-heading {
  font-family: monospace;
  text-align: center;
}

.loginPage {
  background-image: url('https://scontent.famm6-1.fna.fbcdn.net/v/t31.0-8/13254737_1392314914117623_6715261289121393155_o.jpg?_nc_cat=107&_nc_ohc=y9VKo3RLzBMAQmd6BphZxw77pKOegzNiQJIF2ba8Wp0HYUljUwo6KW7tQ&_nc_ht=scontent.famm6-1.fna&oh=af96472fbb280941be07f997c1ccbdf7&oe=5E7D6D24');
  background-size: cover;
  height: 100vh;
  overflow: auto;
}
.loginPage .container {
  margin: auto;
  text-align: center;
  width: 800px;
  height: 100vh;
  display: table-cell;
  vertical-align: middle;
}
.loginPage .description {
  color: #fff;
  font-size: 20px;
}
.loginPage .instructions-wrapper {
  color: #fff;
  text-align: left;
  margin-top: 100px;
}
.loginPage .instructions {
  width: 1150px;
  margin: auto;
  padding-left: 40px;
}
.loginPage .instructions {
  text-align: left;
}
.loginPage #first-time {
  display: block;
  margin: auto;
  text-align: center;
  width: 950px;
  color: #b40381 !important;
  font-weight: bold;
  font-size: 22px;
  font-family: 'Tomorrow', sans-serif;
}
.opacity {
  display: table;
  width: 100%;
  background-color: rgb(0, 0, 0, 0.8);
}
.opacity h1 {
  font-family: 'Tomorrow', sans-serif;
  color: #b40381 !important;
  font-size: 75px;
  font-weight: bold;
  margin-bottom: 10px;
}
.loginPage ul {
  /* list-style: none; */
  padding-left: 0px;
  margin-top: 40px;
  font-size: 18px;
}
.loginPage .github-btn {
  width: 210px !important;
  margin: auto !important;
}

